      v-model="selectedSort"
      :options="sortOptions"
    />-->
  </div>
  <my-dialog v-model:show = "dialogVisible">
    <post-form @create = "createPost"/>
  </my-dialog>
  <post-list 
    v-if = "!isPostsLoading"
    :posts="sortedAndSearchedPosts"
    @remove="removePost"/>
  <div v-else> идет загрузка</div>
