    <div 
      v-for="pageNumber in totalPage"
      :key="pageNumber"
      class="page"
      :class="{
        'current-page': page === pageNumber
      }"
      @click="changePage(pageNumber)"
      >{{ pageNumber }}
    </div>
  </div>-->
</template>

<script>
import PostForm from '@/components/PostForm.vue';
import PostList from '@/components/PostList.vue';
import axios from 'axios';
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex'; // импорт для упращения обращений к доступу мутаций, экшенов. стейта и геттеров
export default {
  components: {
    PostForm, PostList
  },
  data() {
    return {
      dialogVisible: false,
    }
  },
  methods: {
    ...mapMutations({
      setPage: 'post/setPage'
    }),
    ...mapActions({
      loadMorePosts: 'post/loadMorePosts',
      fetchPosts: 'post/fetchPosts'
    }),
    createPost(post){
      this.posts.push(post);
      this.dialogVisible = false;
    },
    removePost(post){
      this.posts = this.posts.filter((p) => p.id !== post.id)
    }, 
    showDialog(){
      this.dialogVisible = true;
    },
    /*changePage(newPageNumber){
      this.page = newPageNumber;
    },*/
  },
  mounted() {
    //this.fetchPosts();
    /*const options = {
      root: document.querySelector("#scrollArea"), // по умолчанию 
      rootMargin: "0px",
      threshold: 1.0,
    };
    const callback = (entries, observer) => { // отработает когда мы пересечем элемент, причем и при скроле вниз, и при скроле вверх
      if(entries[0].isIntersecting && this.page < this.totalPage){
        this.loadMorePosts();
      } // условие для того, чтобы только при скроле вниз работало
    }
    const observer = new IntersectionObserver(callback, options);
    observer.observe(this.$refs.observer);*/
  },
  watch: {
    /*page(){
      this.fetchPosts();
    }*/
    /*selectedSort(newValue){
      this.posts.sort((post1, post2) => post1[this.selectedSort]?.localeCompare(post2[this.selectedSort]))
    }*/
  },
  computed: {
    ...mapState({
      posts: state => state.post.posts, 
      isPostsLoading: state => state.post.isPostsLoading,
      searchQuery: state => state.post.searchQuery,
      selectedSort: state => state.post.selectedSort,
      page: state => state.post.page,
      totalPage: state => state.post.totalPage,
      limitPost: state => state.post.limitPost,
      sortOptions: state => state.post.sortOptions
    }),
    ...mapGetters({
      sortedPosts: 'post/sortedPosts',
      sortedAndSearchedPosts: 'post/sortedAndSearchedPosts ',
    }),
  }
}
</script>

<style>
a,button{
cursor: pointer;
}
.app_btn{
display: flex;
justify-content: space-between;
margin: 15px 0;
}
.page__wrapper{
display: flex;
margin-top: 15px;
}
.page{
border: 1px solid black;
